<template>
    <v-layout justify-center>
        <v-flex xs12>
            <v-toolbar color="white">
            <v-toolbar-title>ENTRADA DE EQUIPOS</v-toolbar-title>
            <v-dialog v-model="modal" max-width="1150px" persistent>
                <v-card>
                    <v-card-title class="primary" style="color: white;"><h3>BUSCAR EQUIPOS</h3></v-card-title>
                     <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12 sm12 md12 lg12 xl12>
                                <v-layout row wrap>   
                                    <v-flex xs12 sm12 md12>
                                        <v-text-field append-icon="search" v-model="search" placeholder="...."  class="text-xs-center vs"  label="Busqueda">
                                        </v-text-field>
                                    </v-flex>
                                </v-layout>
                                <template>
                                    <v-data-table  :search="search"  :headers="cabeceraProducts" :items="product" class="elevation-1">
                                            <template slot="items" slot-scope="props">
                                            <td>
                                                <v-icon color="red darken-2"  small class="mr-2" @click="agregar(props.item)"> add</v-icon>
                                            </td>
                                            <td>{{props.item.idproducto}}</td>
                                            <td>{{props.item.modelo}}</td>
                                            <td>{{props.item.serie}}</td>
                                            <td>{{props.item.digito}}</td>
                                            <td>{{props.item.codbarras}}</td>
                                            <td>{{props.item.stock}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay equipos para mostrar.</h3>
                                            </span>
                                        </template>
                                    </v-data-table> 
                                </template>
                            </v-flex>
                        </v-layout>
                    </v-container>
                    <v-divider></v-divider>
                    <v-card-text>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-btn dark color="red darken" @click="cerrar()"> Cerrar </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
            <v-spacer></v-spacer>
            <v-btn color="brown" dark @click="abrir()">BUSCAR EQUIPOS<v-icon dark >search</v-icon> </v-btn>
            <v-btn color="success" dark @click="agregarRegistro()">REGISTRAR<v-icon dark >add_circle</v-icon> </v-btn>
            </v-toolbar>
            <v-card>
                 <v-container fluid grid-list-md >
                    <v-layout row wrap>
                        <v-flex xs12>
                           <v-card>
                                <v-card-title class="error" style="color: white;margin-top:3px"><h3>ITEM EQUIPOS</h3></v-card-title>
                                        <v-container fluid grid-list-sm>
                                            <v-layout row  wrap>
                                                <v-flex xs6>
                                                    <v-text-field v-model="modelo" type="text" prepend-icon="receipt" label="MODELO DEL EQUIPO"></v-text-field>
                                                </v-flex>
                                                <v-flex xs6>
                                                    <v-text-field v-model="bcodBarras" type="number" prepend-icon="looks_one" label="COD. BARRAS"></v-text-field>
                                                </v-flex>
                                                <v-flex xs6>
                                                    <v-text-field v-model="operador" type="text" prepend-icon="stay_current_portrait" label="OPERADOR (MOVISTAR - BITEL - CLARO)"></v-text-field>
                                                </v-flex>
                                                <v-flex xs6>
                                                    <v-text-field v-model="SIM" type="text" prepend-icon="ballot" label="SIM"></v-text-field>
                                                </v-flex>
                                                <v-flex xs9>
                                                    <v-text-field v-model="accesorios" type="text" prepend-icon="article" label="ACCESORIOS"></v-text-field>
                                                </v-flex>
                                                <v-flex xs3>
                                                    <v-btn color="primary" dark @click="agregarLista" >AGREGAR<v-icon dark >add_circle</v-icon> </v-btn>
                                                </v-flex>
                                                <v-flex xs12 sm12 md12 lg12 xl12>
                                                    <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"> </div></h3>
                                                </v-flex>
                                            </v-layout>
                                        </v-container>
                                        <v-card-text>
                                        <v-data-table :headers="cabeceraEquipos" :items="detalles" hide-actions  class="elevation-1 margen">
                                            <template  slot="items" slot-scope="props">
                                                <td class="margen">
                                                    <v-icon color="red darken-2" small @click="eliminarDetalle(detalles,props.item)">delete</v-icon>
                                                </td>
                                                <td>{{props.item.Modelo}}</td>
                                                <td>{{props.item.Serie}}</td>
                                                <td>{{props.item.Digito}}</td>
                                                <td>{{props.item.SIM}}</td>
                                                <td>{{props.item.Operador}}</td>
                                                <td>{{props.item.EstadoAccesorio}}</td>
                                                <td></td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3> No hay equipos agregados al detalle.</h3>
                                                </span>
                                            </template>
                                        </v-data-table>
                                        <v-flex class="text-xs-right">                                  
                                        </v-flex>
                                    </v-card-text>
                                <v-divider></v-divider>
                           </v-card>
                        </v-flex>
                    </v-layout>
                 </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            valida: 0,
            validaMensaje:[],
            search:'',
            idproducto:'',
            modelo: '',
            bcodBarras:'',
            operador : '',
            SIM: '',
            accesorios: '',
            serie:'',
            digito:'',
            modal:false,
            product: [],            
            detalles:[],
            cabeceraEquipos: [
                    { text: 'OP', value: 'opciones',sortable: false},
                    { text: 'MODELO', value: 'modelo', sortable: true},
                    { text: 'SERIE', value: '',sortable: false},
                    { text: 'DIGITO', value: '',sortable: false},
                    { text: 'SIM', value: '',sortable: false},
                    { text: 'OPERADOR', value: '',sortable: false},
                    { text: 'ACCESORIO', value: '', sortable: false  },       
            ],
            cabeceraProducts:[
                { text: 'SELECCIONAR', value: '',sortable: false},
                { text: '#', value: '',sortable: false},
                { text: 'MODELO', value: 'modelo',sortable: true},
                { text: 'SERIE', value: '',sortable: false},
                { text: 'DIGITO', value: '',sortable: false},
                { text: 'COD BARRAS', value: '',sortable: false},
                { text: 'STOCK', value: '',sortable: false},
            ]
        }
    },
    created() {
       
    },
    methods: {
        cerrar(){
            this.modal = false;
        },
        abrir(){
            this.modal = true;
            this.listarProductp();
        },
        async listarProductp(){
            try {
                let me= this;
                const response = await axios.get('producto/listar');
                me.product = response.data;
            } catch (error) {   
            }
        },
        registrar(){
            
        },
        validar() {
            this.valida=0;
            this.validaMensaje=[];
            if (!this.modelo){
                this.validaMensaje.push("Ingrese Modelo.");
            }
            if (this.codbarras<3){
                this.validaMensaje.push("Ingrese Cod Barras.");
            }
            if (!this.operador){
                this.validaMensaje.push("Ingrese Operador.");
            }
            if (!this.SIM){
                this.validaMensaje.push("Ingrese SIM.");
            }
            if (!this.accesorios){
                this.validaMensaje.push("Ingrese Accesorio.");
            }
            if (this.validaMensaje.length){
                this.valida=1;
            }
            return this.valida;
        },
        agregar(data = []) { 
            this.idproducto = data['idproducto'];
            this.modelo     = data['modelo'];
            this.bcodBarras = data['codbarras'];
            this.serie      = data['serie'];
            this.digito     = data['digito'];
            this.modal = false;
        },
        agregarLista() {
            if(this.validar()){
                return;
            }
            this.detalles.push
            ({
                IdProducto: this.idproducto,
                EstadoAccesorio:  this.accesorios,
                Operador:   this.operador,
                SIM:        this.SIM,
                Modelo:     this.modelo,
                Serie:      this.serie,
                Digito:     this.digito
            });
            this.limpiar();
        },
        limpiar(){
            this.idproducto = '';
            this.modelo     = '';
            this.bcodBarras = '';
            this.serie      = '';
            this.digito     = '';
            this.operador   = '';
            this.SIM        = '';
            this.accesorios = '';
        },
        eliminarDetalle(arr,item){
            var i= arr.indexOf(item);
            if (i!==-1){
                arr.splice(i,1);
            }
        },
        agregarRegistro() {
            let me = this;
            axios.post('entrada',{
                'entradas':me.detalles
            }).then(function(response){
                me.$swal('EXITOSO.', 'Registro Exitoso.', 'success')
                me.detalles = [];
            }).catch(function(error){
            });
        }
    }
}
</script>


<style>
    .tabs{
         padding: 0%;
    }
</style>