<template>
    <v-layout justify-center>
        <v-flex xs12>
            <v-toolbar color="white">
            <v-toolbar-title>SALIDA DE EQUIPOS</v-toolbar-title>
            <v-dialog v-model="modal" max-width="1150px" persistent>
                <v-card>
                    <v-card-title class="primary" style="color: white;"><h3>BUSCAR EQUIPO ENTRADA</h3></v-card-title>
                     <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12 sm12 md12 lg12 xl12>
                                <v-layout row wrap>   
                                    <v-flex xs12 sm12 md12>
                                        <v-text-field append-icon="search" placeholder="...."  class="text-xs-center vs"  label="Busqueda">
                                        </v-text-field>
                                    </v-flex>
                                </v-layout>
                                <template>
                                    <v-data-table :headers="cabeceraEntrada" :items="entrada" class="elevation-1">
                                            <template slot="items" slot-scope="props">
                                            <td>
                                                <v-icon color="red darken-2"  small class="mr-2" @click="agregar(props.item)"> add</v-icon>
                                            </td>
                                            <td>{{props.item.IdEntrada}}</td>
                                            <td>{{props.item.FechaEntrada}}</td>
                                            <td>{{props.item.Modelo}}</td>
                                            <td>{{props.item.Serie}}</td>
                                            <td>{{props.item.Digito}}</td>
                                            <td>{{props.item.EstadoAccesorio}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay equipos para mostrar.</h3>
                                            </span>
                                        </template>
                                    </v-data-table> 
                                </template>
                            </v-flex>
                        </v-layout>
                    </v-container>
                    <v-divider></v-divider>
                    <v-card-text>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-btn dark color="red darken" @click="cerrar()"> Cerrar </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
            <v-spacer></v-spacer>
            <v-btn color="brown" dark @click="abrir()">BUSCAR EQUIPOS<v-icon dark >search</v-icon> </v-btn>
            <v-btn color="success" dark @click="agregarRegistro()">REGISTRAR<v-icon dark >search</v-icon> </v-btn>
            </v-toolbar>
            <v-card>
                 <v-container fluid grid-list-md >
                    <v-layout row wrap>
                        <v-flex xs12>
                           <v-card>
                                <v-card-title class="error" style="color: white;margin-top:3px"><h3>ITEM EQUIPOS</h3></v-card-title>
                                        <v-container fluid grid-list-sm>
                                            <v-layout row  wrap>
                                                <v-flex xs6>
                                                    <v-select v-model="idusuario" :items="usuario" @change="obtenerFuncionario(idusuario)" prepend-icon="assignment" label="FUNCIONARIO"></v-select>
                                                </v-flex>
                                                <v-flex xs6>
                                                    <v-text-field v-model="bcodBarras" type="number" prepend-icon="looks_one" label="COD. BARRAS"></v-text-field>
                                                </v-flex>
                                            </v-layout>
                                        </v-container>
                                        <v-card-text>
                                        <v-data-table :headers="cabeceraEquipos" :items="detalles" hide-actions  class="elevation-1 margen">
                                            <template  slot="items" slot-scope="props">
                                                <td class="margen">
                                                    <v-icon color="red darken-2" small>delete</v-icon>
                                                </td>
                                                <td>{{props.item.FechaEntrada}}</td>
                                                <td>{{props.item.Serie}}</td>
                                                <td>{{props.item.Digito}}</td>
                                                <td>{{props.item.Modelo}}</td>
                                                <td>{{props.item.Funcionario}}</td>
                                                <td><v-text-field v-model="props.item.Observacion" label="Observacion">
                                        </v-text-field></td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3> No hay equipos agregados al detalle.</h3>
                                                </span>
                                            </template>
                                        </v-data-table>
                                        <v-flex class="text-xs-right">                                  
                                        </v-flex>
                                    </v-card-text>
                                <v-divider></v-divider>
                           </v-card>
                        </v-flex>
                    </v-layout>
                 </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            selected: '',
            modal:false,
            entrada: [],
            func:'',
            idusuario:'',
            bcodBarras:'',
            detalles:[],
            usuario:[],
            cabeceraEquipos: [
                    { text: 'OP', value: 'opciones',sortable: false},
                    { text: 'FECHA INGRESO', value: '',sortable: false},
                    { text: 'SERIE', value: '',sortable: false},
                    { text: 'DIGITO', value: '',sortable: false},
                    { text: 'MODELO', value: '',sortable: false},
                    { text: 'EJECUTIVO', value: '',sortable: false},
                    { text: 'OBSERVACION DE LA SALIDA', value: '', sortable: false  },       
            ],
            cabeceraEntrada:[
                { text: 'SELECCIONAR', value: '',sortable: false},
                { text: '#', value: '',sortable: false},
                { text: 'FECHA ENTRADA', value: '',sortable: false},
                { text: 'MODELO EQUIPO', value: '',sortable: false},
                { text: 'SERIE', value: '',sortable: false},
                { text: 'DIGITO', value: '',sortable: false},
                { text: 'ACCESORIO', value: '',sortable: false},
            ]
        }
    },
    created() {
        this.selectUsuario();  
    },
    methods: {
        selectUsuario(){
            let me=this;
            var uArray=[];
            axios.get('usuario/listar').then(function(response){
                uArray=response.data;
                uArray.map(function(x){
                    me.usuario.push({text: x.name,value:x.idusuario});
                });
            }).catch(function(error){
            });
        },
        cerrar(){
            this.modal = false;
        },
        abrir(){
            if(!this.idusuario){
                  this.$swal('ADVERTENCIA.', 'Seleccione Funcionario.', 'warning')
            }else{
                this.modal = true;
                this.listarEntrada();
            }
        },
        async listarEntrada(){
            try {
                let me= this;
                const response = await axios.get('entrada/listar');
                me.entrada = response.data;
            } catch (error) {   
            }
        },
        registrar(){
            console.log(this.detalles);
        },
        async obtenerFuncionario(id) {
            try {
                let me= this;
                const response = await axios.get('usuario/listar');
                let u = response.data;
                me.func = u.filter(x=>x.idusuario == id); 
            } catch (error) {   
            }
        },
        agregar(data = []) { 
            this.detalles.push
            ({
                IdProducto:data['IdProducto'],
                IdEntrada:data['IdEntrada'],
                IdUsuario:this.func[0].idusuario,
                Funcionario:this.func[0].name,
                FechaEntrada:data['FechaEntrada'],
                Modelo:data['Modelo'],
                Serie:data['Serie'],
                Digito:data['Digito'],
                Observacion:'-' 
            });
            this.modal = false;
        },
        agregarRegistro() {
            let me = this;
            axios.post('salida',{
                'salidas':me.detalles
            }).then(function(response){
                me.$swal('EXITOSO.', 'Registro Exitoso.', 'success')
                me.detalles = [];
            }).catch(function(error){
            });
        }
    }
}
</script>


<style>
    .tabs{
         padding: 0%;
    }
</style>