<template>
    <v-layout align-center justify-center>
        <v-flex xs12 sm8 md6 lg5 xl4>
            <v-card>
                <v-toolbar dark flat color="blue darken-2">
                    <v-toolbar-title>
                        INICIA SESION PMP
                    </v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                        <v-flex text-center>
                            <v-text-field v-model="email"  prepend-icon="person" color="primary" label="EMAIL" required></v-text-field>
                            <v-text-field v-model="clave"  prepend-icon="lock"   type="password" color="primary" label="PASSWORD" required></v-text-field>
                            <v-flex class="red--text" v-if="error">
                                <h3> {{error}} </h3>
                            </v-flex>
                         </v-flex>
                </v-card-text>
                <v-card-actions class="px-3 pb-3">
                    <v-flex text-xs-right>
                        <div class="text-center">
                             <v-btn color="error" dark @click="ingresar()">INGRESAR <v-icon dark >check_circle</v-icon> </v-btn>
                        </div>
                    </v-flex>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import axios from 'axios'
export default {
    data(){
        return {
            email: '',
            clave: '',
            error:null
        }
    },
    methods: {
        async ingresar(){
            try {
                let me = this;
                me.error=null;
                const resp = await axios.post('usuario/login', {'email': me.email, 'password': me.clave});
                let respuesta = resp.data;
                me.$store.dispatch("guardarToken", respuesta.token);
                me.$router.push({name: 'home'});
            } catch (err) {
                let me = this;
                if (err.response.status==400){
                    me.error="No es usuario vlido";
                } else if (err.response.status==404){
                    me.error="No existe el usuario o sus datos son incorrectos";
                }else{
                    me.error="Ocurri un error";
                } 
            }
        }
    }    
}
</script> 