import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Categoria from './components/categorias/Index.vue';
import Rol from './components/roles/Index.vue';
import Usuario from './components/seguridad/Index.vue';
import Productos from './components/productos/Index.vue';
import Salidas from './components/salidas/Create.vue';
import Login from './components/seguridad/Login.vue'
import ReporteSalida from './components/reportes/Salidas.vue'
import ReporteEntrada from './components/reportes/Entrada.vue'
import Entrada from './components/entradas/Create.vue'

Vue.use(Router)

var router =  new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta :{
        funcionario :true,
        ejecutivo: true,
        asistente: true
      }
    },
    {
      path: '/categorias',
      name: 'categorias',
      component: Categoria,
      meta :{
        funcionario :true,
        ejecutivo: true,
        asistente: true
      }
    },
    {
      path: '/reporte_entrada',
      name: 'reporte_entrada',
      component: ReporteEntrada,
      meta :{
        funcionario :true,
        ejecutivo: true,
        asistente: true
      }
    },
    {
      path: '/prod',
      name: 'prod',
      component: Productos,
      meta :{
        funcionario :true,
        ejecutivo: true,
        asistente: true
      }
    },
    {
      path: '/roles',
      name: 'roles',
      component: Rol,
      meta :{
        funcionario :true,
        ejecutivo: true,
        asistente: true
      }
    },
    {
      path: '/usuarios',
      name: 'usuarios',
      component: Usuario,
      meta :{
        funcionario :true,
        ejecutivo: true,
        asistente: true
      }
    },
    {
      path: '/salidas',
      name: 'salidas',
      component: Salidas,
      meta :{
        funcionario :true,
        ejecutivo: true,
        asistente: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta : {
        libre: true
      }
    },
    {
      path: '/reporte_salidas',
      name: 'reporte_salidas',
      component: ReporteSalida,
      meta :{
        funcionario :true,
        ejecutivo: true,
        asistente: true
      }
    },
    {
      path: '/entrada',
      name: 'entrada',
      component: Entrada,
      meta :{
        funcionario :false,
        ejecutivo: true,
        asistente: true
      }
    },
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.libre))
  {
    next()
  }
  else
  {
    next()
  }
})

export default router