<template>
    <v-dialog v-model="modal" max-width="600px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;" primary-title>
                <h3>NUEVO USUARIO</h3>
            </v-card-title>
            <v-card-text> 
                <v-container grid-list-md>
                    <v-layout wrap>
                        <v-flex xs12 sm12 md12>
                            <v-text-field v-model="name" label="NOMBRE"></v-text-field>
                            <v-autocomplete :items="roles" v-model="idrol" label="ROL" >
                            </v-autocomplete>
                            <v-text-field  v-model="email" label="EMAIL"></v-text-field>
                            <v-text-field  v-model="password" type="password" label="PASSWORD"></v-text-field>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="error" @click="cancelar()">CANCELAR <v-icon dark right>block</v-icon></v-btn>
                <v-btn color="info" @click="save()">GUARDAR<v-icon dark right>check_circle</v-icon></v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios';
export default {
    data() {
        return {
            roles:[],
            modal: false,
            name: '',
            idrol:'',
            email:'',
            password: ''
        }
    },
    created(){
        this.modal = true;
        this.select();
    },
    methods: {
        cancelar(){
            this.modal = false;
            this.$emit('cerrar');
        },
        save (){
            let me = this;
            axios.post('usuario/save',{
                'IdRol':me.idrol,
                'Name':me.name,
                'Email':me.email,
                'Password':me.password
            }).then(function(response){
                me.modal = false;
                me.$emit('cerrar');
                me.$swal('EXITOSO.', 'Registro Exitoso.', 'success')
            }).catch(function(error){
            });
        },
        select(){
            let me=this;
            var rolArray=[];
            axios.get('rol/listar').then(function(response){
                rolArray=response.data;
                rolArray.map(function(x){
                    me.roles.push({text: x.descripcion,value:x.idrol});
                });
            }).catch(function(error){
            });
        }
    }
}
</script>