<template>
    <v-dialog v-model="modal" max-width="500px" persistent>
        <v-card>
            <v-card-title class="error" style="color: white;" primary-title>
                <h3>CAMBIAR CONTRASEA</h3>
            </v-card-title>
            <v-card-text> 
                <v-container grid-list-md>
                    <v-layout wrap>
                        <v-flex xs12 sm12 md12>
                            <v-text-field  type="password" v-model="password" label="CAMBIAR PASSWORD"></v-text-field>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="error" @click="cancelar()">CANCELAR <v-icon dark right>block</v-icon></v-btn>
                <v-btn color="info" @click="updated()">GUARDAR<v-icon dark right>check_circle</v-icon></v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios';
export default {
    props: { 
        user: {
          type: Object
        }
    },
    data() {
        return {
            modal: false,
            password: '',
            estado :'change'
        }
    },
    created(){
        this.modal = true;
    },
    methods: {
        cancelar(){
            this.modal = false;
            this.$emit('cerrar', this.estado);
        },
        updated() {
            let me = this;
            axios.put('usuario/cambiar',{
                'IdUsuario':me.user.idusuario,
                'Password': me.password,
            }).then(function(response){
                me.cancelar();
                me.$swal('EXITOSO.', 'Contrasea Cambiando Exitosamente.', 'success')
            }).catch(function(error){
            });
        }
    }
}
</script>