<template>
    <v-dialog v-model="modal" max-width="600px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;" primary-title>
                <h3>EDITAR USUARIO</h3>
            </v-card-title>
            <v-card-text> 
                <v-container grid-list-md>
                    <v-layout wrap>
                        <v-flex xs12 sm12 md12>
                            <v-text-field   v-model="user.name" label="NOMBRE"></v-text-field>
                            <v-autocomplete v-model="user.idrol" :items="roles" label="ROL" ></v-autocomplete>
                            <v-text-field   v-model="user.email" label="EMAIL"></v-text-field>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="error" @click="cancelar()">CANCELAR <v-icon dark right>block</v-icon></v-btn>
                <v-btn color="info" @click="updated()">GUARDAR<v-icon dark right>check_circle</v-icon></v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios';
export default {
    props: { 
        user: {
          type: Object
        }
    },
    data() {
        return {
            estado :'edit',
            modal: false,
            roles: []
        }
    },
    created(){
        this.modal = true;
        this.select();
    },
    methods: {
        cancelar(){
           this.modal = false;
           this.$emit('cerrar', this.estado);
        },
        updated() {
            let me = this;
            axios.put('usuario/actualizar',{
                'IdUsuario':me.user.idusuario,
                'IdRol': me.user.idrol,
                'Name':me.user.name,
                'Email':me.user.email
            }).then(function(response){
                me.cancelar();
            }).catch(function(error){
            });
        },
        select(){
            let me=this;
            var rolArray=[];
            axios.get('rol/listar').then(function(response){
                rolArray=response.data;
                rolArray.map(function(x){
                    me.roles.push({text: x.descripcion,value:x.idrol});
                });
            }).catch(function(error){
            });
        }
    }
}
</script>