<template>
    <v-layout justify-center>
        <v-flex xs12>
            <v-toolbar color="white">
            <v-toolbar-title>SALIDA DE EQUIPOS</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn color="brown" dark @click="excel()">EXCEL<v-icon dark >search</v-icon> </v-btn>            
            </v-toolbar>
            <v-card>
                 <v-container fluid grid-list-md >
                    <v-layout row wrap>
                        <v-flex xs12>
                           <v-card>
                                <v-card-title class="error" style="color: white;margin-top:3px"><h3>REPORTE SALIDA EQUIPOS</h3></v-card-title>
                                    <v-card-text>
                                        <v-data-table :headers="cabeceraEquipos" :items="salidas" hide-actions  class="elevation-1 margen">
                                            <template  slot="items" slot-scope="props">
                                                <td>{{props.item.FechaEntrada}}</td>
                                                <td>{{props.item.FechaSalida}}</td>
                                                <td>{{props.item.Modelo}}</td>
                                                <td>{{props.item.Usuario}}</td>
                                                <td>{{props.item.Operador}}</td>
                                                <td>{{props.item.SIM}}</td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3> No hay equipos agregados al detalle.</h3>
                                                </span>
                                            </template>
                                        </v-data-table>
                                        <v-flex class="text-xs-right">                                  
                                        </v-flex>
                                    </v-card-text>
                                <v-divider></v-divider>
                           </v-card>
                        </v-flex>
                    </v-layout>
                 </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            salidas:[],
            cabeceraEquipos: [
                    { text: 'FECHA INGRESO', value: '',sortable: false},
                    { text: 'FECHA SALIDA', value: '',sortable: false},
                    { text: 'MODELO', value: '',sortable: false},
                    { text: 'USUARIO', value: '',sortable: false},
                    { text: 'OPERADOR', value: '', sortable: false  }, 
                    { text: 'SIM', value: '', sortable: false  },       
            ],
        }
    },
    created() {
        this.listarSalida();  
    },
    methods: {
        async listarSalida(){  
            try {
                let me= this;
                const response = await axios.get('salida');
                me.salidas = response.data;
            } catch (error) {   
            }
        },
        excel(){
            window.open('https://fast-peru.com/inventario/public/api/salida/excel', '_blank');
        }
    }
}
</script>

<style>
    .tabs{
         padding: 0%;
    }
</style>