<template>
   <v-layout justify-center>
        <v-flex xs12>
             <v-toolbar dense> 
                <v-toolbar-title>INFORMACION USUARIO</v-toolbar-title>
                <create v-if="ismodal" @cerrar="cancelar"/>
                <edit   v-if="isedit" :user="usuario"   @cerrar="cancelar"/>
                <change v-if="ischange" :user="usuario" @cerrar="cancelar"/>
                <v-spacer></v-spacer>
                <v-btn  color="primary" dark  @click="nuevo()">NUEVO<v-icon dark >add</v-icon></v-btn>
             </v-toolbar>
             <v-divider></v-divider>
             <v-card>
                <v-container fluid grid-list-md>
                    <v-layout row wrap>
                        <v-flex xs12>
                            <v-card>
                                <v-card-title>
                                    LISTA DE USUARIOS
                                    <v-spacer></v-spacer>
                                    <v-text-field append-icon="mdi-magnify" single-line hide-details></v-text-field>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-data-table
                                    :headers="usuarios"
                                    :items="user"
                                    class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                        <td>
                                             <v-icon small color="red darken-2" @click="chagepass(props.item)" >delete</v-icon>
                                            <v-icon small color="primary darken-2" @click="obtenerUsuario(props.item.idusuario)">edit</v-icon>
                                        </td>
                                        <td>{{props.item.idusuario}}</td>
                                        <td>{{props.item.rol}}</td>
                                        <td>{{props.item.name}}</td>
                                        <td>{{props.item.email}}</td>
                                        <td v-if="props.item.estado==1">Activo</td>
                                        <td v-else>Desactivado</td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3> No hay usuarios.</h3>
                                        </span>
                                    </template>
                                </v-data-table>
                            </v-card>
                         </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import axios from 'axios'
import create from './Create.vue'
import edit from './Edit.vue'
import change from './ChangePassword.vue'

export default {
    data() {
        return { 
            ischange:false,
            isedit:false,
            ismodal:false,   
            user:[],
            usuario:null,
            usuarios: [
                { text: 'ACCION', value: '',sortable: false},
                { text: 'ID', value: '',sortable: false},
                { text: 'ROL', value: '',sortable: false},
                { text: 'NOMBRE', value: '',sortable: false},
                { text: 'EMAIL', value: '',sortable: false},
                { text: 'ESTADO', value: '',sortable: false},
            ]
        }
    },
    components: {
        create, edit, change
    },
    created() {
        this.getUsuario();  
    },
    methods: {
        chagepass(item){
            this.ischange = true;
            this.usuario = item;
        },
        async getUsuario (){
            try {
                let me= this;
                const response = await axios.get('usuario/listar');
                me.user = response.data;
            } catch (error) {   
            }
        },
        nuevo(){
             this.ismodal = true;
        },
        cancelar(estado){
            if(estado =='edit'){
                this.isedit = false;
                this.getUsuario();
            }
            else if(estado =='change'){
                this.ischange = false;
                this.getUsuario();
            }
            else{
                this.ismodal= false;
                this.getUsuario();  
            }
        },
        async obtenerUsuario(idusuario) {
            try {
                let me= this;
                const response = await axios.get('usuario/' + idusuario);
                me.isedit = true;
                me.usuario = response.data;
            } catch (error) {
            }
        }
    }
}
</script>