<template>
    <v-dialog v-model="modal" max-width="1000px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;" primary-title>
                <h3>NUEVO EQUIPO</h3>
            </v-card-title>
            <v-card-text> 
                <v-container grid-list-md>
                    <v-layout wrap>
                        <v-flex xs4 sm16 md6>
                            <v-autocomplete :items="categoria" v-model="idcategoria" label="CATEGORIA" ></v-autocomplete> 
                        </v-flex>
                        <v-flex xs4 sm16 md6>
                            <v-text-field v-model="modelo" label="MODELO"></v-text-field>
                        </v-flex>
                        <v-flex xs4 sm16 md12>
                             <v-text-field  v-model="caracteristicas" label="DESCRIPCION"></v-text-field>
                        </v-flex>
                        <v-flex xs4 sm16 md6>
                            <v-text-field  v-model="serie" label="SERIE"></v-text-field>
                        </v-flex>
                        <v-flex xs4 sm16 md6>
                            <v-text-field v-model="stock" label="STOCK"></v-text-field>
                        </v-flex>
                        <v-flex xs4 sm16 md12 >
                            <label class="text-reader">
                                <h5>Cargar Imagen</h5>
                                <input type="file" @change="onFileChange">
                            </label>
                            <label class="text-reader" @click="removeImage">
                                <h5>Quitar Imagen</h5>
                            </label>
                            <div v-if="!image">
                                <v-card>
                                    <v-img :src="image" class="my-3" contain height="180"></v-img>
                                </v-card> 
                            </div>
                            <div v-else>
                                <v-card>
                                    <v-img :src="image" class="my-3" contain height="180"></v-img>
                                </v-card>
                            </div>
                        </v-flex>
                        <v-flex xs4 sm16 md12>
                            <v-text-field  type="text" v-model="codbarras" label="COD BARRAS" value="0"></v-text-field>
                            <barcode :value="codbarras" :options="{format:'EAN-13'}" ><h3>Generando codigo barras</h3></barcode>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="error" @click="cancelar()">CANCELAR <v-icon dark right>block</v-icon></v-btn>
                <v-btn color="info" @click="guardarEquipo()">GUARDAR<v-icon dark right>check_circle</v-icon></v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
import axios from 'axios';
import VueBarcode from 'vue-barcode';
export default {
    data() {
        return {  
            image: '', 
            photo:'',
            modelo:'',
            caracteristicas:'',
            digito:'',
            serie:'',
            codbarras:'',
            stock:'',
            idcategoria:'',  
            categoria:[],
            modal: false,
        }
    },
    components: {
        'barcode': VueBarcode
    },
    created() {
         this.modal = true;
         this.selectCategoria();
    },
    methods:{
        cancelar(){
            this.modal = false;
            this.$emit('cerrar');
        },
        selectCategoria(){
            let me=this;
            var catArray=[];
            axios.get('categoria/listar').then(function(response){
                catArray=response.data;
                catArray.map(function(x){
                    me.categoria.push({text: x.descripcion,value:x.idcategoria});
                });
            }).catch(function(error){
            });
        },
        onFileChange(e) {
            var files = e.target.files || e.dataTransfer.files;
            this.photo = e.target.files[0];
            if (!files.length)
                return;
            this.createImage(files[0]);
        },
        createImage(file) {
            var image = new Image();
            var reader = new FileReader();
            var vm = this;
            reader.onload = (e) => {
                vm.image = e.target.result;
            };
            reader.readAsDataURL(file);
        },
        removeImage: function (e) {
            this.image = '';
        },
        guardarEquipo(){
            let data = new FormData();
            data.append('photo', this.photo);
            data.append('modelo', this.modelo);
            data.append('caracteristicas', this.caracteristicas);
            data.append('serie', this.serie);
            data.append('codbarras', this.codbarras);
            data.append('stock', this.stock);
            data.append('idcategoria', this.idcategoria);
            let config = { header : {'Content-Type' : 'multipart/form-data'}}

            axios.post('producto/registrar', data, config).then(response => { 
                this.cancelar();
                this.$swal('EXITOSO.', 'Registro Exitoso.', 'success')
            });
        }
    }
}
</script>

<style>
    .text-reader {
        position: relative;
        overflow: hidden;
        display: inline-block;

        /* Fancy button style ?? */
        border: 2px solid white;
        border-radius: 0px;
        padding: 8px 12px;
        cursor: pointer;
        background-color: red;
        color: white;
    }
     .text-reader input {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        opacity: 0;
    }
    img {
        width: 30%;
        margin: auto;
        display: block;
        margin-bottom: 10px;
    }
</style>