<template>
   <v-layout justify-center>
        <v-flex xs12>
             <v-toolbar dense> 
                <v-toolbar-title>INFORMACION EQUIPOS</v-toolbar-title>
                <create v-if="ismodal" @cerrar="cancelar"/>
                <v-spacer></v-spacer>
                <v-btn  color="primary" dark  @click="nuevo()">NUEVO<v-icon dark>add</v-icon></v-btn>
             </v-toolbar>
             <v-divider></v-divider>
             <v-card>
                <v-container fluid grid-list-md>
                    <v-layout row wrap>
                        <v-flex xs12>
                            <v-card>
                                <v-card-title>
                                    LISTA DE EQUIPOS
                                    <v-spacer></v-spacer>
                                    <v-text-field append-icon="mdi-magnify" single-line hide-details></v-text-field>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-data-table
                                    :headers="equip"
                                    :items="equipos"
                                    class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                        <td>
                                            <v-icon small color="primary darken-2">edit</v-icon>
                                        </td>
                                        <td>{{props.item.idproducto}}</td>
                                        <td>{{props.item.modelo}}</td>
                                        <td>{{props.item.categoria}}</td>
                                        <td>{{props.item.serie}}</td>
                                        <td>{{props.item.codbarras}}</td>
                                        <td>{{props.item.stock}}</td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3> No hay equipos.</h3>
                                        </span>
                                    </template>
                                </v-data-table>
                            </v-card>
                         </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import create from './Create.vue'
import axios from 'axios'
export default {
    data() {
        return {
            ismodal:false,
            equipos: [],
            equip: [
                { text: 'ACCION', value: '',sortable: false},
                { text: '#', value: '',sortable: false},
                { text: 'MODELO', value: '',sortable: false},
                { text: 'CATEGORIA', value: '',sortable: false},
                { text: 'SERIE', value: '',sortable: false},
                { text: 'CODBARRAS', value: '',sortable: false},
                { text: 'STOCK', value: '',sortable: false}
            ]
        }
    },
    components: {
        create
    },
    created() {
        this.getProductos();
    },
    methods: {
        async getProductos (){
            try {
                let me= this;
                const response = await axios.get('producto/listar');
                me.equipos = response.data;
            } catch (error) {   
            }
        },
        nuevo(){
            this.ismodal=true;
        },
        cancelar(){
            this.ismodal= false;
            this.getProductos();
        }
    }
}
</script>